        var el4 = dom.createElement("h1");
        var el5 = dom.createTextNode("Visual Pool: A Tool to Visualize and Interact with the Pooling Method");
        dom.appendChild(el4, el5);
        dom.appendChild(el3, el4);
        var el4 = dom.createTextNode("\n    ");
        dom.appendChild(el3, el4);
